import { Model } from '@vuex-orm/core'

export default class Game extends Model {
  static entity = 'games'
  static primaryKey = 'tag'

  static fields () {
    return {
      title: this.string(),
      icon: this.string(),
      logo: this.string(),
      tag: this.string(),
      installed: this.boolean(false),
      latestVersion: this.string(null)
    }
  }
}
