import Vue from 'vue'
import Notifications from 'vue-notification'
import App from './App.vue'
import router from './router'
import store from './store'
import wInput from '@/w-input'
import wBtn from '@/w-btn'
import init from 'init'

Vue.component('w-input', wInput)
Vue.component('w-btn', wBtn)

Vue.use(Notifications)

init()

const originallog = console.log
const originalerror = console.error

const debugLog = (message, type, originalFn) => {
  store.dispatch('ui/debugLog', {
    message,
    type
  })
  if (originalFn) originalFn(message)
}

console.log = message => debugLog(message, 'log', originallog)
console.error = message => debugLog(message, 'error', originalerror)

// Notification.requestPermission()

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
