<template>
  <div
    v-if="showPeek"
    class="peekContainer"
    @mousedown.stop="checkClose"
  >
    <div class="overlay" />
    <div
      v-if="layout"
      class="peek"
    >
      <component
        :is="layout"
        v-if="layout"
        :key="layout"
      />
    </div>
  </div>
</template>
<script>
// eslint-disable-next-line no-unused-vars
export default {
  components: {
    profile: () => import('./profile/index.vue'),
    gameSettings: () => import('./gameSettings/index.vue')
  },
  computed: {
    showPeek () {
      return this.$store.getters['ui/showPeek']
    },
    peekInfo () {
      return this.$store.getters['ui/peekInfo']
    },
    layout () {
      var layout = null
      switch (this.showPeek) {
        case 'profile':
          layout = 'profile'
          break
        case 'game':
          layout = 'gameSettings'
          break
      }
      return layout
    }
  },
  methods: {
    checkClose (i) {
      if (i.target === this.$el) this.closePeek()
    },
    closePeek () {
      this.$store.dispatch('ui/setPeek', false)
    }
  }
}
</script>
<style lang="stylus">
.peekContainer {
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  overflow: hidden;
  background-color: rgba(black, 0.75);
}

.peek {
  background-color: rgba($primary-1, 95%);
  width: 480px;
  position: absolute;
  top: 0;
  right: -480px;
  height: 100vh;
  animation-name: slideIn;
  animation-duration: 0.15s;
  animation-fill-mode: forwards;
  animation-timing-function: ease-out;
  overflow: hidden;

  .title {
    display: inline;
    font-family: eurostile, sans-serif;
    font-weight: 400;
    font-size: 38px;
  }

  .titleBar {
    height: 48px;
    padding: 16px;

    img {
      width: 45px;
      vertical-align: bottom;
      border-radius: 100%;
      padding: 2.5px;
    }
  }

  .header {
    font-family: politica, sans-serif;
    font-weight: 700;
    margin-bottom: 8px;

    &:nth-child(n+2) {
      margin-top: 16px;
    }
  }

  .footer {
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 16px;
  }
}

@keyframes slideIn {
  100% {
    right: 0;
  }
}
</style>
