<template>
  <div
    v-if="showLoadingModal"
    class="container"
  >
    <div class="modal">
      <div class="fa-3x">
        <i class="fas fa-spinner fa-spin" />
      </div>
      <span style="white-space: pre;">
        {{ loadingModalMessage || 'Loading...' }}
      </span>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'LoadingModal',
  computed: {
    ...mapGetters({
      showLoadingModal: 'ui/showLoadingModal',
      loadingModalMessage: 'ui/loadingModalMessage'
    })
  }
}
</script>
<style lang="stylus" scoped>
.container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: alpha(black, 0.5);
  z-index: 9999;
}

.modal {
  position: absolute;
  width: 300px;
  height: 100px;
  left: calc(50vw - 150px);
  top: calc(50vh - 50px);
  text-align: center;
}
</style>
