/* eslint-disable no-undef */
import Game from 'store/models/game'
import notify from 'utils/notify'
import store from 'store'

const getters = {
  games: () => Game.all()
}

const actions = {
  async play ({ commit }, { gameTag, params }) {
    var game = Game.find(gameTag)
    var user = store.getters['authentication/user']

    // Do a little sanity check with lan and username
    if (params && params.includes('lan') && !params.includes('+name')) {
      console.log('no name set! changing param...')
      params = params + ` +name "${user.username || 'Unknown Solider'}"`
    }

    store.dispatch('ui/setLoadingState', {
      show: true,
      message: `Launching ${game.title || gameTag}` + (params ? `\nUsing Params: ${params}` : '')
    })
    try {
      const token = await createSession(game.tag || gameTag)
      if (!launcherLaunchGame(gameTag, token, params)) throw new Error('An error occured launching the game.')
    } catch (ex) {
      notify.create({
        title: 'Failed to launch game',
        text: ex,
        type: 'error'
      })
    }
  },
  async selectDir ({ commit }, tag) {
    const path = launcherSelectGameFolder(tag)
    return path
  },
  async verifyOwnership ({ commit }, tag) {
    const error = launcherVerifyOwnership(tag)
    if (error) {
      notify.create({
        title: 'Failed to verify ownership:',
        text: error,
        type: 'error'
      })
    } else {
      notify.create({
        title: 'Verifying ownership...'
      })
    }
  }
}

export default {
  getters,
  actions
}
