/* eslint-disable no-undef */
import router from 'router'
import store from 'store'
import notify from 'utils/notify'

const state = {
  authenticated: false,
  user: {
    username: 'unknown user',
    profilePic: null,
    friends: []
  },
  remember: false
}

const getters = {
  authentication: state => state,
  authenticated: state => state.authenticated,
  remember: state => state.remember,
  user: state => state.user,
  userFriends: state => state.user.friends
}

async function setupUserInformation () {
  state.user.username = getUsername()
  state.user.profilePic = getAvatar()
  router.push({ path: '/' })
}

const actions = {
  async authenticate ({ commit }, { username, password, remember }) {
    store.dispatch('ui/setLoadingState', { show: true, message: 'Authenticating...' })
    try {
      var authenticated = await login(username, password)
      if (authenticated) {
        if (!authenticated.emailVerified) {
          notify.create({
            title: 'Your email is unverified!',
            text: "Verify your email on the forum to ensure you don't lose access to your account.",
            type: 'warn',
            duration: 10000
          })
        }

        launcherSetToken()
        commit('saveAuth', { authenticated, remember: remember })
        await setupUserInformation()
        store.dispatch('ui/setLoadingState', { show: false })
      }
    } catch (error) {
      console.error(`NIX: ${error}`)
      notify.create({
        title: 'An issue occured when logging in.',
        text: error,
        type: 'error'
      })
      store.dispatch('ui/setLoadingState', { show: false })
    }
  },
  async logout ({ commit }) {
    // eslint-disable-next-line no-undef
    try {
      launcherSetToken('')
      commit('saveAuth', { authenticated: false })
      router.push({ path: '/login', query: { logout: true } })
    } catch (error) {
      console.error(`NIX: ${error}`)
      notify.create({
        title: 'An issue occured when logging out.',
        text: error,
        type: 'error'
      })
      store.dispatch('ui/setLoadingState', { show: false })
    }
  },
  async preAuthed ({ commit }) {
    try {
      const authed = await validateToken()
      if (authed) {
        if (!authed.emailVerified) {
          notify.create({
            title: 'Your email is unverified!',
            text: "Verify your email on the forum to ensure you don't lose access to your account.",
            type: 'warn',
            duration: 10000
          })
        }

        commit('saveAuth', { authenticated: true })
        await setupUserInformation()
        store.dispatch('ui/setLoadingState', { show: false })
      } else {
        launcherSetToken('')
        router.push({ path: '/login' })
        store.dispatch('ui/setLoadingState', { show: false })
      }
    } catch (error) {
      console.error(`NIX: ${error}`)
      notify.create({
        title: 'An issue occured when logging in using saved credentials.',
        text: error,
        type: 'error'
      })
      store.dispatch('ui/setLoadingState', { show: false })
    }
  },
  async loadFriends ({ commit }) {
    commit('saveFriends', [])
    store.dispatch('ui/setLoadingState', { show: true, message: 'Loading Friends...' })
    try {
      var friends = await getFriends()
      commit('saveFriends', friends)
      store.dispatch('ui/setLoadingState', { show: false })
    } catch (error) {
      console.error(`NIX: ${error}`)
      notify.create({
        title: 'An issue occured when getting friends.',
        text: error,
        type: 'error'
      })
      store.dispatch('ui/setLoadingState', { show: false })
    }
    // store.dispatch('ui/refreshPeek')
  }
}

const mutations = {
  saveAuth (state, { authenticated, remember }) {
    state.authenticated = !!authenticated
    state.remember = !!remember
  },
  saveFriends (state, friends) {
    state.user.friends = friends
  }
}

export default {
  state,
  getters,
  actions,
  mutations,
  namespaced: true
}
