<template>
  <div>
    <div
      v-if="showDebugInfo"
      class="debugWindow"
    >
      <pre>Debug Info:</pre>
      <pre v-html="debugJson" />
    </div>
    <div v-if="showDebugInfo" id="consoleWindow" class="consoleWindow">
      <pre>Console</pre>
      <pre
        v-for="(log, index) in $store.getters['ui/debugConsole']"
        :key="`consoleMessage${index}`"
        :class="`consoleMessage ${log.type}`"
        v-html="`[${log.timestamp}] ${log.message}`"
      />
    </div>
  </div>
</template>
<script>
import store from 'store'
import { mapGetters } from 'vuex'
const openDebugMenu = (event) => {
  const keyCode = event.keyCode
  if (keyCode === 113) store.dispatch('ui/toggleDebugInfo')
}

const clipboardFunctionality = (event) => {
  const keyCode = event.keyCode
  if (event.getModifierState('Control') && keyCode === 86) {
    // eslint-disable-next-line no-undef
    const pasteString = launcherGetClipboard()
    const activeElement = document.activeElement
    const cursorPos = activeElement.selectionStart
    const originalValue = String(activeElement.value)
    var newValue = pasteString
    if (originalValue) {
      if (cursorPos === 0) newValue = pasteString + originalValue
      else if (cursorPos === originalValue.length) newValue = originalValue + pasteString
      else newValue = originalValue.substr(0, cursorPos) + pasteString + originalValue.substr(cursorPos)
    }
    if (activeElement.tagName === 'INPUT') activeElement.value = newValue
    activeElement.dispatchEvent(new Event('paste'))
  }
}

document.addEventListener('keyup', openDebugMenu)
document.addEventListener('keydown', clipboardFunctionality)
export default {
  computed: {
    ...mapGetters({
      showDebugInfo: 'ui/showDebugInfo'
    }),
    debugJson () {
      return {
        Authenticated: this.$store.getters['authentication/authenticated'],
        RememberUser: this.$store.getters['authentication/remember'],
        AuthedUser: this.$store.getters['authentication/user'],
        peek: {
          show: this.$store.getters['ui/showPeek'],
          info: this.$store.getters['ui/peekInfo']
        }
      }
    }
  },
  beforeDestroy () {
    document.removeEventListener('keyup', openDebugMenu)
  }
}
</script>
<style lang="stylus" scoped>
.debugWindow, .consoleWindow {
  position: absolute;
  display: block;
  z-index: 100;
  background-color: alpha(black, 0.95);
  padding: 0px 16px 0px 16px;
  overflow: auto;
  border: 1px solid alpha(white, 0.15);
}

.debugWindow {
  top: 0;
  left: 0;
  bottom: 26%;
  overflow-y: scroll;
}

.consoleWindow {
  left: 0;
  top: 75%;
  right: 0;
  bottom: 0;
  overflow-y: scroll;

  .consoleMessage {
    padding: 8px 4px 8px 4px;
    margin: 0;
    border-bottom: 1px solid alpha(white, 0.2);

    &.error {
      color: tint(red, 25%);
    }

    &.warning {
      color: tint(orange, 25%);
    }
  }
}
</style>
