<template>
  <input
    v-if="!disable"
    :ref="`input-${name}`"
    v-model="input"
    class="input"
    :name="name"
    v-bind="$attrs"
    @keyup.enter="$emit('enter')"
    @paste="emitRawValue"
  >
  <div
    v-else-if="$attrs.type !== 'password'"
    class="input"
  >
    {{ value }}
  </div>
  <div
    v-else
    class="input"
  >
    {{ value | passwordFilter }}
  </div>
</template>
<script>
export default {
  name: 'WInput',
  filters: {
    passwordFilter (value) {
      const replacer = '•'
      return replacer.repeat(value.length)
    }
  },
  props: {
    value: {
      type: [String, Number]
    },
    disable: {
      type: Boolean
    },
    name: {
      type: String,
      required: true
    }
  },
  computed: {
    input: {
      get () {
        return this.value
      },
      set (val) {
        this.$emit('input', val)
        this.$emit('change', val)
      }
    }
  },
  methods: {
    emitRawValue () {
      var el = this.$refs[`input-${this.name}`]
      this.$emit('input', el.value)
      this.$emit('change', el.value)
    }
  }
}
</script>
<style lang="stylus" scoped>
.input {
  background-color: alpha(white, 0.05);
  border: 0;
  border-bottom: 0.75px solid alpha(black, 0.2);
  height: 46px;
  font-family: eurostile, sans-serif;
  font-size: 18px;
  margin: 4px 8px 4px 8px;
  padding-left: 4px;
  padding-right: 4px;
  width: calc(100% - 24px);
  color: darken(white, 20%);

  &:hover {
    background-color: alpha(black, 0.05);
  }

  &:focus {
    background-color: alpha(black, 0.1);
  }
}

div.input {
  line-height: 43px;
  margin-top: 8px;
}
</style>
