<template>
  <div
    class="button clickable"
    :class="{ disable }"
    v-bind="$attrs"
    @click="clickEvent"
  >
    <div v-if="label">
      {{ label }}
    </div>
  </div>
</template>
<script>
export default {
  name: 'WBtn',
  props: {
    label: {
      type: String
    },
    disable: {
      type: Boolean
    }
  },
  methods: {
    clickEvent (e) {
      if (!this.disable) this.$emit('click', e)
    }
  }
}
</script>
<style lang="stylus" scoped>
.button {
  font-family: politica, sans-serif;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  display: inline-block;
  background-color: $info;
  color: white;
  padding: 8px;
  margin: 8px;
  height: 20px;
  text-align: center;
  cursor: pointer;

  &.disable {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.text {
  margin-top: 3px;
}
</style>
