import notify from 'utils/notify'

const state = {
  showPeek: false,
  peekInfo: null,
  debug: {
    enabled: true,
    show: process.env.VUE_APP_ENV === 'development' || false,
    log: []
  },
  loading: {
    show: false,
    message: null
  }
}

const getters = {
  showPeek: state => state.showPeek,
  peekInfo: state => state.peekInfo,
  showDebugInfo: state => state.debug.show,
  debugConsole: state => state.debug.log,
  showLoadingModal: state => state.loading.show,
  loadingModalMessage: state => state.loading.message
}

const actions = {
  setPeek ({ commit }, route) {
    if (route !== undefined) {
      if (typeof route === 'string' && route.includes('/')) {
        var splitRoute = route.split('/')
        commit('setPeekInfo', splitRoute[1])
        commit('setPeek', splitRoute[0])
      } else {
        commit('setPeekInfo', null)
        commit('setPeek', route)
      }
    } else {
      commit('setPeek', !state.showPeek)
    }
  },
  toggleDebugInfo ({ commit }) {
    commit('toggleDebugInfo')
  },
  debugLog ({ commit }, { message, type }) {
    commit('debugLog', { message, type })
  },
  enableDebug ({ commit }) {
    commit('enableDebug')
  },
  setLoadingState ({ commit }, { show, message }) {
    commit('setLoadingModal', { show, message })
  },
  refreshPeek ({ commit, state }) {
    const previousPeek = state.showPeek
    commit('setPeek', false)
    commit('setPeek', previousPeek)
  }
}

const mutations = {
  setPeek (state, showing) {
    state.showPeek = showing
  },
  setPeekInfo (state, info) {
    state.peekInfo = info
  },
  toggleDebugInfo (state) {
    if (state.debug.show) state.debug.show = false
    else if (state.debug.enabled) state.debug.show = true
    notify.create({
      title: `Debug mode is: ${state.debug.show ? 'ON' : 'OFF'}`
    })
  },
  debugLog (state, { message, type }) {
    var d = new Date()
    state.debug.log.push({
      timestamp: d.toTimeString().substr(0, 8),
      message: message,
      type: type || 'log'
    })
    var consoleWin = document.getElementById('consoleWindow')
    if (consoleWin) consoleWin.scrollTop = consoleWin.scrollHeight
  },
  enableDebug (state) {
    state.debug.enabled = true
    notify.create({
      title: "I'm a little butterfly!"
    })
  },
  setLoadingModal (state, { show, message }) {
    state.loading.show = show
    state.loading.message = message || null
  }
}

export default {
  state,
  getters,
  actions,
  mutations,
  namespaced: true
}
