import Vue from 'vue'
import VueRouter from 'vue-router'
import store from 'store'

Vue.use(VueRouter)

const isAuthed = (to, from, next) => {
  var isLoggedIn = store.getters['authentication/authenticated']
  if (isLoggedIn) {
    next({ name: 'dashboard' })
  } else {
    next()
  }
}

const isNotAuthed = (to, from, next) => {
  if (!store.getters['authentication/authenticated']) {
    next({ name: 'login' })
  } else {
    next()
  }
}

const routes = [
  {
    path: '/login',
    name: 'login',
    component: () =>
      import(/* webpackChunkName: "auth" */ '../views/login'),
    meta: {
      layout: 'blank'
    },
    beforeEnter: isAuthed
  },
  {
    path: '/logout',
    name: 'logout',
    meta: {
      layout: 'blank'
    },
    beforeEnter: () => {
      store.dispatch('authentication/logout')
    }
  },
  {
    path: '/game/:gameTag',
    name: 'games',
    meta: {
      layout: 'basic'
    },
    component: () => import(/* webpackChunkName: "games" */ 'views/game'),
    props: true,
    beforeEnter: isNotAuthed
  },
  {
    name: 'catch-all',
    path: '*',
    redirect: '/game/iw5mp',
    meta: {
      layout: 'blank'
    }
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
